// import 
import sunuiStar from '@/components/sunui-star/sunui-star.vue';
export default {
	components:{
		'sunui-star':sunuiStar
	},
	data() {
		return {
			start:4,
			ImagUrl:"",
			ImagUrl1:"",
			ImagUrl2:""
		}
	},
	methods: {
	
		Upload(){
			let that = this
			uni.chooseImage({
			    count: 6, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album',"camera"], //从相册选择
			    success: function (res) {
			        console.log(JSON.stringify(res.tempFilePaths));
					
					that.ImagUrl = res.tempFilePaths[0]
					
			    }
			});
		},
		Upload1(){
			let that = this
			uni.chooseImage({
			    count: 6, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album'], //从相册选择
			    success: function (res) {
			        console.log(JSON.stringify(res.tempFilePaths));
					
					that.ImagUrl1 = res.tempFilePaths[0]
					
			    }
			});
		},
		Upload2(){
			let that = this
			uni.chooseImage({
			    count: 6, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album'], //从相册选择
			    success: function (res) {
			        console.log(JSON.stringify(res.tempFilePaths));					
					that.ImagUrl2 = res.tempFilePaths[0]
			    }
			});
		},
		changeStar(e){
			console.log(e.curStar)
			this.start=e.curStar
		},
		contentChange(e){
			console.log(e.detail.text)
		}
	}
}
